import React from 'react'
import './About.scss'
import LogoMobile from '../LogoMobile'

const About = () => {
    return (
        <>
            <div className="white-space" />
            <LogoMobile />
            <section className="about">
                <h2 className="about__title">O mnie</h2>
                <p className="about__text">
                    Hej! Mam na imię Paulina. Witaj na mojej stronie sznurkowych królowych.
                    Znajdziesz tutaj piękne torebki, plecaki i inne dodatki, jak opaski do włosów oraz koszyki ze sznurka.
                    <br />
                    <br />
                    Moja pasja rozpoczęła się właśnie od koszyczków. Zachwycona ilością splotów i kolorów chciałam stworzyć coś wyjątkowego. Zaczęłam szydełkować i nie mogłam już przestać. 😊
                    <br />
                    <br />
                    Wszystkie rzeczy wykonuję ręcznie z wysokiej jakości przędzy bawełnianej T-shirt yarn premium wyprodukowanej w Polsce.
                    Zamawiając produkty na stronie możesz je spersonalizować poprzez wybór koloru sznurka lub galanterii metalowej (łańcuszki, karabińczyki).
                    <br />
                    <br />
                    Stwórz wyjątkową rzecz dla siebie i dołącz do grona sznurkowych królowych!
                </p>
            </section>
        </>
    )
}

export default About
